@import url(https://fonts.googleapis.com/css?family=Muli:400,400i,700,700i);
h1 {font-family: 'Muli', "Helvetica Neue", sans-serif; font-weight: 700; letter-spacing: 2.5px; text-transform: uppercase; font-size: 2em;}
h2 {font-family: 'Muli', "Helvetica Neue", sans-serif; font-weight: 700; letter-spacing: 2px; text-transform: uppercase; font-size: 1em;}
h3 {font-family: 'Muli', "Helvetica Neue", sans-serif; font-weight: 700; letter-spacing: 2px; text-transform: uppercase; font-size: .6em;}
h4 {font-family: 'Muli', "Helvetica Neue", sans-serif; font-weight: 700; font-size: 1em;}
h5 {font-family: 'Muli', "Helvetica Neue", sans-serif; font-weight: 700; font-size: .6em;}
h6 {font-family: 'Muli', "Helvetica Neue", sans-serif; font-weight: 700; text-transform: capitalize; font-size: .6em;}

#root{
  background-color: black;
  z-index: -5;
}

.front{
  z-index: 1000;
  position: absolute;

}

.body-text{
  font-family: 'Muli', "Helvetica Neue", sans-serif; font-weight: 400; font-size: .5em; text-align: left;
}


body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: black;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.hero-video iframe{
  position: fixed;
  width: 100%;
  height: 100%;
  opacity: .5;
}

.hero-video {
  background-color: black;
  position: fixed;
}

.header-text {
  position: relative;
  display: flex;
  text-align: center;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
  min-width: 100vw;
  min-height: 100vh;
}

.header-wrapper {
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.header-text h3 {
  margin-top: -20px;
  letter-spacing: 2.9px;
}

.scroll-indicator {
  -webkit-animation: fade-in-e 10s ease;
  animation: fade-in-e 10s ease;
  opacity: .8;
}

@-webkit-keyframes fade-in-e {
  from{
    opacity: 0;
  }
  to {
    opacity: .8;
  }
}

@keyframes fade-in-e {
  from{
    opacity: 0;
  }
  to {
    opacity: .8;
  }
}


@-webkit-keyframes fade-out {
  from{
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}


@keyframes fade-out {
  from{
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}


