}

nav {
  margin-top: 25px;
  width: 95%;
  text-align: right;
}

.navlink {
  color: #aaa;
  text-decoration: none;
  font-family: 'Muli', "Helvetica Neue", sans-serif;
  font-weight: 700;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-size: .925em;
  padding: 0 2%;
}

.navlink:hover {
  -webkit-animation: color-change .25s ease-in;
          animation: color-change .25s ease-in;
  color: #07918a;

}

#name {
  font-size: 1em;
  margin-top: 15px;
  text-align: left;
  color: white;
  text-decoration: none;
  font-family: 'Muli', "Helvetica Neue", sans-serif;
  font-weight: 700;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-size: 1em;
  position: absolute;
  margin-left: 1vh;
}

@-webkit-keyframes fade-in {
  from{
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fade-in {
  from{
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes color-change {
  from{
    color: #aaa;
  }
  to {
    color: #07918a;
  }
}

@keyframes color-change {
  from{
    color: #aaa;
  }
  to {
    color: #07918a;
  }
}

@-webkit-keyframes expand-down {
  from {
    height: 0%;
  }
  to {
    height: 5vh;
  }
}

@keyframes expand-down {
  from {
    height: 0%;
  }
  to {
    height: 5vh;
  }
}


