  animation: fade-in-c .75s ease forwards;
  -webkit-animation: fade-in-c .75s ease forwards;
  animation-delay: .75s;
  -webkit-animation-delay: .75s;
}

@keyframes enter {
  from {width:0%;}
  to {width: 34%;}
}
@-webkit-keyframes enter {
  from {width:0%;}
  to {width: 34%;}
}

@keyframes fade-in-c {
  from{
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes fade-in-c {
  from{
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}


